
.map-nav{
    position: absolute;
    z-index: 2;
    top: 9rem;
    left: 50%;
    transform: translateX(-50%);
  
    &__list{
      position: absolute;
      top: 0;
      left: 50%;
      width: 100%;
      text-align: center;
      transform: translateX(-50%);
      .icon-mapNav{
        stroke: var(--color-white);
        width: 2rem;
        height: 2rem;
      }
     
    }
  
    &__item{
      text-align: center;
      display: inline-block;
      cursor: pointer;
     
        margin: 0 3.1rem;
      
      span{
        display: block;
        color: var(--color-white);
        font-size: 1.1rem;
        font-family: var(--font-book);
      }
    }
  }
  
  .location-details{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 4rem;
    height: 100vh;
    align-items: flex-end;
    padding-bottom: 5rem;
  
    &__start{
      .btn{
        display: block;
        margin-top: 3rem;
      }
    }
  
    &__end{
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      .icon-share {
        width: 3rem;
        height: 3rem;
        margin-bottom: 2.8rem;
        stroke: var(--color-white);
        fill: transparent;
      }
  
      .icon-distance{
        width: 1.2rem;
        height: 1.2rem;
        position: absolute;
        top: 1.5rem;
        left: -1.5rem;
  
      }
  
      .distance-info{
        position: relative;
        &__number{
          font-size: 3.4rem !important;
        }
        color: var(--color-white);
        span{
          display: inline-block;
          font-size: 2.1rem;
        }
      }
    }
    &__info{
      margin-top: 2rem;
    }
  
    &__info-group{
      span{
        display: inline-block;
        font-size: 1.5rem;
        &:first-child{
          color: var(--color-white);
          margin-inline-end: 1rem;
          
        }
        &:last-child{
          color: var(--color-light-blue);
          font-family: var(--font-medium);
          text-transform: uppercase;
        }
      }
    }
    &__header{
      display: flex;
      flex-wrap: wrap;
    }
  
    &__number{
        min-width: 5rem;
        min-height: 5rem;
        background-color: var(--color-orange);
        color: var(--color-white);
        font-family: var(--font-medium);
        font-size: 2.8rem;
        display: flex;
        justify-content: center;
        align-items: center;
        align-self: center;
    }
  
    &__title{
      margin-inline-start: 1.8rem;
      
      &__main{
        font-size: 2.8rem;
        font-family: var(--font-medium);
        color: var(--color-white);
        display: block;
      }
  
      &__sub{
        font-size: 2rem;
        font-family: var(--font-regular);
        color: #E8E8E8;
      }
    }
  
    &__icon-info-list{
     margin-top: 2rem;
     display: inline-flex;
     align-items: center;
  
    }
  
    &__icon{
      width: 5rem;
      height: 5rem;
      display: inline-block;
      border-radius: 50%;
      font-size: 1.3rem;
      color: var(--color-white);
      display: inline-flex;
      justify-content: center;
      align-items: center;
      &:not(:last-child){
        margin-inline-end: 1.5rem;
      }
      &--black{
        background-color: var(--color-black);
        font-family: var(--font-medium);
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        span{
          display: block;
          &:last-child{
            font-size: 1rem;
          }
        }
      }
  
      &--camp{
        background-color: #285A75;
      }
      &--climp{
        background-color: #EF8D50;
      }
      &--hiking{
        background-color: #C6CA00;
      }
    }
  }