// -----------------------------------------------------------------------------
// This file contains styles that are specific to the home page.
// -----------------------------------------------------------------------------



.section-image{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    height: 100vh;
    position: relative;
  }
  
  .slider-logo{
    height: 13.6rem !important;
    width: max-content !important;
    position: absolute;
    left: 4rem;
    top:50%;
    transform: translateY(-50%);
    text-align: left !important;
  }
  
  .image-overColor{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.39);
    position: absolute;
    z-index: 1;
  }
  .video-control{
    position: absolute;
    z-index: 1;
    bottom: 4rem;
    right: 4rem;
  
    span{
      display: inline-block;
      color: var(--color-white);
      font-size: 1.6rem;
      cursor: pointer;
    }
  }
  #section1{
    overflow: hidden;
    background-position: center;
    background-size: cover;
    position: relative;
    }
    .section-content{
    /*border:1px solid #F00;*/
      width:100%;
      margin:0 auto;
      text-align:center;
      overflow: hidden;
  
      h1{
        text-transform: uppercase;
        font-size: 10rem;
        font-family: var(--font-bold);
        color: var(--color-white);
        line-height: 1.1;
  
        span{
          display: block;
          font-size: 4rem;
          line-height: 1;
        }
      }
  
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
        overflow: hidden;
      }
    }
    h1{
      margin:0;
      color:#333;
    }
    h1 i.fa{
    font-size:3em;
    color:#FFF;
    transition:all 0.3s ease-in-out;
    }
    i.fa:hover{
    color:#f00;
    cursor:pointer;
    transform:scale(1.2) rotate(10deg);
    }  
    #section2{
    background-color:#0F0; 
    }
    #section3{
    background-color:#0ff; 
    }
    .slide{
    background-size:cover;
      background-position:center;
    }  
    #slide1{
    background-image:url(http://sites.psu.edu/siowfa15/wp-content/uploads/sites/29639/2015/10/cat.jpg);
    }  
    #section4{
    background-color:#999; 
    }
    #fp-nav ul li a span, 
    .fp-slidesNav ul li a span {
      width: 16px;
      height: 16px;
      margin: -4px 0 0 -4px;
      background: transparent;
      border: 1px solid #ffffff;
    }
    
    #fp-nav ul li a.active span, 
    .fp-slidesNav ul li a.active span, 
    #fp-nav ul li:hover a.active span, 
    .fp-slidesNav ul li:hover a.active span {
      width: 16px;
      height: 16px;
      margin: -4px 0 0 -4px;
      background: white;
      box-sizing: border-box;
      border: 1px solid #ffffff;
    }
  
  
  
  
  
  
    .explore-btn{
      background-color: var(--color-orange);
      color: var(--color-white);
      bottom: 7.1rem;
      padding: 1.2rem 2rem;
      min-width: 25.9rem;
      border-radius: 2.5rem;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      text-align: center;
      cursor: pointer;
      text-transform: uppercase;
      font-family: var(--font-bold);
      font-size: 1.6rem;
      letter-spacing: 4px;
    }
  
    .side-nav{
      width: 8rem;
      position: absolute;
      right: 4rem;
      top: 50%;
      transform: translateY(-50%);
      margin-top: 3.5rem;
  
      &__small-img{
        width: 7rem;
        height: 4.2rem;
        border-radius: 5px;
      }
  
      span{
        font-family: var(--font-medium);
        color: var(--color-white);
        display: inline-block;
        font-size: 1.4rem;
      }
      
     ol{
       text-align: center;
       margin: 0 auto;
       list-style: none;
  
       li{
         list-style: none;
         cursor: pointer;
        &:not(:last-child){
          margin-bottom: 3.2rem;
        }
       }
    
      }
      .map-btn{
        background-color: var(--color-drak-blue);
        width: 7rem;
        border-radius: 1.5rem;
        text-align: center;
        padding: 6px;
        vertical-align: middle;
      }
    }
  
  //   #section4{
  //     min-height: calc(100vh + 30rem) !important;
  // }
  
  #section1 #menu{
    display:none !important;
  }
  
  
  
  body.fp-viewing-secondSection .side-nav{
    display:none !important;
  }